.tr-drop-effect {
  background-color: #f8f8f8;
}

.star-empty {
  color: #d0d0d0;
}

.dir-icon {
  position: relative;
  text-align: center;
  max-width: 24px;
}

.dir-icon .locked {
  position: absolute;
  width: 1rem;
  height: 1rem;
  top: 50%;
  left: 50%;
}

.tag-list-title {
  overflow: hidden;
}
/* KEEPER */
.small-icon {
  height: 15px;
  width: 15px;
  margin-bottom: 3px;
}

.hover-keep {
  height: 32px;
  pointer-events: auto !important;
  display: inline-block;
  &:hover {
    visibility: visible !important;
    opacity: 1 !important;
  }
}

#bloxberg {
  background-color: #f1f1f1 !important;
  border: 2px solid gray !important;
  text-align: center !important;
  padding: 6px 6px !important;
  line-height: 93%;
}

.tooltip-title {
  color: #b02318;
  font-weight: bold;
  display: block;
  font-size: 12px;
  margin-bottom: 3px;
}

.tooltip-bold {
  color: black;
  font-weight: bold;
  font-size: 12px;
}

.tooltip-nomal {
  color: black;
  font-weight: normal;
  font-size: 12px;
}

.tooltip-nomal a {
  font-weight: bold;
  font-size: 12px;
}
